/**
 * api接口统一管理,每个模块对应相应的api接口
 */
import { post } from '../http';//引入httpjs中的post方法

// 站位发料
export const sendMateByFeeder = p => post('order/sendMateByFeeder', p);

//发料轮询
export const feederOut = p => post('order/feederOut', p);

//新增工单
export const newWorkOrder = p =>post('order/newWorkOrder',p)

//工单列表
export const orderList = p =>post('order/orderList',p)

// 编辑工单
export const updateOrderQty = p =>post('order/updateOrderQty',p)

//删除工单
export const delOrder = p =>post('order/delOrder',p)

//工单分析，工单发料
export const sendMateByOrder = p =>post('order/sendMateByOrder',p)

//工单详情。工单回溯
export const orderDetail = p =>post('order/orderDetail',p)



