/**   
 * api接口统一管理,每个模块对应相应的api接口
 */
import { post } from '../http';//引入httpjs中的post方法


// 料架列表
export const shelfList = p => post('shelf/shelfList', p);

// 取消告警
export const cancelWarning = p => post('shelf/cancelWarning', p);

// 撤销亮灯
export const recallLighted = p => post('shelf/recallLighted', p);

//修改钢网架配置文件
export const updateShelfConfig =p =>post('shelf/updateShelfConfig', p);

// 钢网架服务配置
export const configList =p =>post('shelf/configList', p);

// 钢网架配置保存
export const updateServerConfig =p =>post('shelf/updateServerConfig', p);

//离线钢网架列表
export const offlineShelfList =p =>post('shelf/offlineShelfList', p);

// 获取在线钢网架列表
export const onlineShelfList = p =>post('shelf/onlineShelfList', p);


// 查询库位料架
export const warehouseCategoryNameList = p =>post('shelf/warehouseCategoryNameList', p);

//根据用户权限查询库位
export const warehouseNameList = p =>post('shelf/warehouseNameList', p);

// 查询未绑定的物料架列表
export const unboundShelfList = p =>post('shelf/unboundShelfList', p);

//物料架绑定库位
export const shelfBoundWarehouseName = p =>post('shelf/shelfBoundWarehouseName', p);

//清空物料架
export const clearShelfData = p =>post('shelf/clearShelfData', p);

//删除物料架
export const removeShelf = p =>post('shelf/removeShelf', p);

//增加库位
export const addWarehouseName = p =>post('shelf/addWarehouseName', p);

//修改库位
export const updateWarehouseName = p =>post('shelf/updateWarehouseName', p);

//删除库位
export const delWarehouseName = p =>post('shelf/delWarehouseName', p);

// 根据查仓库类别
export const searchWarehouseCategory =p =>post('shelf/searchWarehouseCategory',p);

// 根据仓库查库位
export const searchWarehouseNameByCategory =p =>post('shelf/searchWarehouseNameByCategory',p);



