/**   
 * api接口统一管理,每个模块对应相应的api接口
 */
import { post } from '../http';//引入httpjs中的post方法

// 用户列表
export const userList = p => post('user/userList', p);

// 增加用户
export const addUser = p => post('user/addUser', p);

//删除用户
export const delUser = p => post('user/delUser', p);

// 用户权限组
export const userGroupList = p =>post('user/userGroupList', p);

//修改用户
export const adminChangeUserPasswdGroup = p =>post('user/adminChangeUserPasswdGroup', p);

//修改个人密码
export const changeUserPasswd = p =>post('/user/changeUserPasswd', p);

// 用户回溯
export const userActionRecord = p =>post('/user/userActionRecord', p);

//管理员修改用户信息
export const adminChangeUserInfo = p =>post('/user/adminChangeUserInfo', p);






