<template>
	<div class="light">
		<div class="pc_light_title">
			<span>{{$t('storage.lights_tip')}}</span>
		</div>
		<div class="light_list">
		<!-- 	<span class="light_one"></span>
			<span class="light_two"></span> -->
			<span :class="this.$store.state.color==3?'light_three_active':'light_three' " @click="colorThree()"></span>
			<span :class="this.$store.state.color==4?'light_four_active':'light_four' "   @click="colorFour()"></span>
			<span :class="this.$store.state.color==5?'light_five_active':'light_five' "   @click="colorFive()"></span>
			<span :class="this.$store.state.color==6?'light_six_active':'light_six' "   @click="colorSix()"></span>
		</div>
	</div>
</template>
<script>
 import { mapMutations } from 'vuex'
	export default{
		data(){
			return{

			}
		},
		methods:{
			...mapMutations(['changeColor']),
			colorThree(key){
				this.changeColor({ color: 3});		
			},
			colorFour(key){
				this.changeColor({ color: 4});	
			},
			colorFive(key){
				this.changeColor({ color: 5});	
			},
			colorSix(key){
				this.changeColor({ color: 6});	
			},
		}
	}
</script>


